import React, { useState, useEffect } from 'react';
import { Quote, Star, ChevronLeft, ChevronRight } from 'lucide-react';
import { testimonials } from '../data/mock';

const TestimonialsSection = () => {
  const [currentTestimonial, setCurrentTestimonial] = useState(0);
  const [isAutoPlaying, setIsAutoPlaying] = useState(true);

  useEffect(() => {
    if (!isAutoPlaying) return;

    const timer = setInterval(() => {
      setCurrentTestimonial((prev) => (prev + 1) % testimonials.length);
    }, 6000);

    return () => clearInterval(timer);
  }, [isAutoPlaying]);

  const nextTestimonial = () => {
    setCurrentTestimonial((prev) => (prev + 1) % testimonials.length);
    setIsAutoPlaying(false);
  };

  const prevTestimonial = () => {
    setCurrentTestimonial((prev) => (prev - 1 + testimonials.length) % testimonials.length);
    setIsAutoPlaying(false);
  };

  const goToTestimonial = (index) => {
    setCurrentTestimonial(index);
    setIsAutoPlaying(false);
  };

  const current = testimonials[currentTestimonial];

  return (
    <section className="py-20 bg-gradient-to-b from-black to-gray-900">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Section Header */}
        <div className="text-center mb-16">
          <p className="text-silver/60 text-sm font-mono tracking-widest uppercase mb-4">
            Client Testimonials
          </p>
          <h2 className="text-4xl md:text-6xl font-bold text-silver mb-6">
            What Our Clients
            <span className="block gradient-text">Say About Us</span>
          </h2>
          <p className="text-gray-400 text-xl max-w-3xl mx-auto leading-relaxed">
            Don't just take our word for it. Hear from the enterprise leaders who have 
            transformed their businesses with our solutions.
          </p>
        </div>

        {/* Testimonial Carousel */}
        <div className="relative max-w-5xl mx-auto">
          <div className="glass-morphism rounded-2xl p-8 md:p-12 relative overflow-hidden">
            {/* Background Quote Icon */}
            <div className="absolute top-4 right-4 opacity-10">
              <Quote size={120} className="text-silver" />
            </div>

            {/* Testimonial Content */}
            <div className="relative z-10">
              {/* Stars */}
              <div className="flex justify-center mb-6">
                {[...Array(current.rating)].map((_, i) => (
                  <Star key={i} size={24} className="text-yellow-400 fill-current mx-1" />
                ))}
              </div>

              {/* Quote */}
              <blockquote className="text-xl md:text-2xl text-silver leading-relaxed text-center mb-8 font-light">
                "{current.quote}"
              </blockquote>

              {/* Author Info */}
              <div className="flex items-center justify-center">
                <div className="w-16 h-16 bg-gradient-to-br from-silver to-gray-400 rounded-full flex items-center justify-center mr-4">
                  <span className="text-black font-bold text-lg">{current.avatar}</span>
                </div>
                <div className="text-left">
                  <div className="text-silver font-semibold text-lg">{current.name}</div>
                  <div className="text-gray-400">{current.position}</div>
                  <div className="text-gray-500 text-sm">{current.company}</div>
                </div>
              </div>
            </div>

            {/* Navigation Arrows */}
            <button
              onClick={prevTestimonial}
              className="absolute left-4 top-1/2 transform -translate-y-1/2 w-12 h-12 bg-silver/20 hover:bg-silver/30 rounded-full flex items-center justify-center transition-all duration-300 hover:scale-110"
            >
              <ChevronLeft size={24} className="text-silver" />
            </button>
            <button
              onClick={nextTestimonial}
              className="absolute right-4 top-1/2 transform -translate-y-1/2 w-12 h-12 bg-silver/20 hover:bg-silver/30 rounded-full flex items-center justify-center transition-all duration-300 hover:scale-110"
            >
              <ChevronRight size={24} className="text-silver" />
            </button>
          </div>

          {/* Testimonial Indicators */}
          <div className="flex justify-center mt-8 space-x-3">
            {testimonials.map((_, index) => (
              <button
                key={index}
                onClick={() => goToTestimonial(index)}
                className={`w-3 h-3 rounded-full transition-all duration-300 ${
                  index === currentTestimonial 
                    ? 'bg-silver scale-125' 
                    : 'bg-silver/30 hover:bg-silver/50'
                }`}
              />
            ))}
          </div>
        </div>

        {/* Additional Testimonials Grid */}
        <div className="mt-20 grid md:grid-cols-3 gap-8">
          {testimonials.map((testimonial, index) => (
            <div
              key={testimonial.id}
              className={`bg-gradient-to-br from-gray-900 to-black p-6 rounded-xl border transition-all duration-300 cursor-pointer ${
                index === currentTestimonial 
                  ? 'border-silver shadow-2xl' 
                  : 'border-gray-700 hover:border-gray-600'
              }`}
              onClick={() => goToTestimonial(index)}
            >
              {/* Mini Stars */}
              <div className="flex mb-3">
                {[...Array(testimonial.rating)].map((_, i) => (
                  <Star key={i} size={14} className="text-yellow-400 fill-current mr-1" />
                ))}
              </div>

              {/* Mini Quote */}
              <p className="text-gray-400 text-sm mb-4 line-clamp-3">
                "{testimonial.quote.substring(0, 120)}..."
              </p>

              {/* Mini Author */}
              <div className="flex items-center">
                <div className="w-8 h-8 bg-gradient-to-br from-silver to-gray-400 rounded-full flex items-center justify-center mr-3">
                  <span className="text-black font-bold text-xs">{testimonial.avatar}</span>
                </div>
                <div>
                  <div className="text-silver font-medium text-sm">{testimonial.name}</div>
                  <div className="text-gray-500 text-xs">{testimonial.company}</div>
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* Trust Indicators */}
        <div className="mt-20 text-center">
          <div className="grid grid-cols-2 md:grid-cols-4 gap-8">
            <div className="glass-morphism rounded-lg p-4">
              <div className="text-2xl font-bold text-silver mb-1">100%</div>
              <div className="text-gray-400 text-sm">Client Satisfaction</div>
            </div>
            <div className="glass-morphism rounded-lg p-4">
              <div className="text-2xl font-bold text-silver mb-1">5.0</div>
              <div className="text-gray-400 text-sm">Average Rating</div>
            </div>
            <div className="glass-morphism rounded-lg p-4">
              <div className="text-2xl font-bold text-silver mb-1">95%</div>
              <div className="text-gray-400 text-sm">Client Retention</div>
            </div>
            <div className="glass-morphism rounded-lg p-4">
              <div className="text-2xl font-bold text-silver mb-1">24/7</div>
              <div className="text-gray-400 text-sm">Support Response</div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default TestimonialsSection;