import { createRootRoute, createRoute, Outlet } from "@tanstack/react-router";
import HomePage from "../components/HomePage";
import HiringPage from "../components/hiringPage/HiringPage";
import TalentPoolPage from "../components/talent/TalentPoolPage.jsx"; // optional
import AdminLogin from "../components/admin/AdminLogin";
import AdminLayout from "../components/admin/AdminLayout";
import AdminDashboard from "../components/admin/AdminDashboard";
import LeadManagement from "../components/admin/LeadManagement";
import TestingDashboard from "../components/TestingDashboard";

const rootRoute = createRootRoute({
  component: () => <>{<Outlet />}</>,
});

const homeRoute = createRoute({
  getParentRoute: () => rootRoute,
  path: "/",
  component: HomePage,
});

const hiringRoute = createRoute({
  getParentRoute: () => rootRoute,
  path: "/hiring",
  component: HiringPage,
});

const talentPoolRoute = createRoute({
  getParentRoute: () => rootRoute,
  path: "/talent/$roleId",
  component: TalentPoolPage,
});

const testingDashboardRoute = createRoute({
  getParentRoute: () => rootRoute,
  path: "/testing",
  component: TestingDashboard,
});

// Admin routes
const adminLoginRoute = createRoute({
  getParentRoute: () => rootRoute,
  path: "/admin/login",
  component: AdminLogin,
});

const adminLayoutRoute = createRoute({
  getParentRoute: () => rootRoute,
  path: "/admin",
  component: AdminLayout,
});

const adminDashboardRoute = createRoute({
  getParentRoute: () => adminLayoutRoute,
  path: "/dashboard",
  component: AdminDashboard,
});

const adminLeadsRoute = createRoute({
  getParentRoute: () => adminLayoutRoute,
  path: "/leads",
  component: LeadManagement,
});

export const routeTree = rootRoute.addChildren([
  homeRoute,
  hiringRoute,
  talentPoolRoute,
  testingDashboardRoute,
  adminLoginRoute,
  adminLayoutRoute,
  adminDashboardRoute,
  adminLeadsRoute,
]);
