import React, { useState } from 'react';
import { ExternalLink, Code2, Smartphone, Monitor } from 'lucide-react';
import { showcaseApps } from '../data/mock';

const ShowcaseSection = () => {
  const [hoveredApp, setHoveredApp] = useState(null);

  return (
    <section id="showcase" className="py-20 bg-black">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Section Header */}
        <div className="text-center mb-16">
          <p className="text-silver/60 text-sm font-mono tracking-widest uppercase mb-4">
            Our Work
          </p>
          <h2 className="text-4xl md:text-6xl font-bold text-silver mb-6">
            Application
            <span className="block gradient-text">Showcase</span>
          </h2>
          <p className="text-gray-400 text-xl max-w-3xl mx-auto leading-relaxed">
            Explore our portfolio of enterprise applications, each crafted with precision 
            and designed to solve complex business challenges.
          </p>
        </div>

        {/* Showcase Grid */}
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8 mb-16">
          {showcaseApps.map((app, index) => (
            <div
              key={app.id}
              className="group relative bg-gradient-to-br from-gray-900 to-black rounded-xl overflow-hidden border border-gray-700 hover:border-gray-600 transition-all duration-500 card-hover-effect"
              onMouseEnter={() => setHoveredApp(index)}
              onMouseLeave={() => setHoveredApp(null)}
            >
              {/* App Preview Area */}
              <div className="relative h-48 bg-gradient-to-br from-gray-800 to-gray-900 flex items-center justify-center">
                {/* Mock App Interface */}
                <div className="relative w-full h-full overflow-hidden">
                  <div className="absolute inset-0 grid grid-cols-6 grid-rows-4 opacity-20">
                    {Array.from({ length: 24 }).map((_, i) => (
                      <div
                        key={i}
                        className="border border-gray-600 transition-colors duration-300"
                        style={{
                          borderColor: hoveredApp === index ? app.color : 'rgb(75 85 99)',
                          animationDelay: `${i * 0.1}s`
                        }}
                      ></div>
                    ))}
                  </div>
                  <div className="absolute inset-0 flex items-center justify-center">
                    <Monitor size={48} style={{ color: app.color }} />
                  </div>
                </div>

                {/* Hover Overlay */}
                <div className={`absolute inset-0 bg-gradient-to-t from-black/80 to-transparent transition-opacity duration-300 ${
                  hoveredApp === index ? 'opacity-100' : 'opacity-0'
                }`}>
                  <div className="absolute bottom-4 left-4 right-4">
                    <button className="w-full bg-silver text-black py-2 px-4 rounded-lg font-semibold text-sm hover:bg-white transition-colors duration-300 flex items-center justify-center gap-2">
                      View Demo
                      <ExternalLink size={16} />
                    </button>
                  </div>
                </div>
              </div>

              {/* App Details */}
              <div className="p-6">
                <div className="flex items-start justify-between mb-4">
                  <h3 className="text-xl font-bold text-silver group-hover:text-white transition-colors duration-300">
                    {app.name}
                  </h3>
                  <div className="flex gap-1">
                    <Code2 size={16} className="text-gray-400" />
                    <Smartphone size={16} className="text-gray-400" />
                  </div>
                </div>

                <p className="text-gray-400 text-sm mb-4 leading-relaxed">
                  {app.description}
                </p>

                {/* Technologies */}
                <div className="mb-4">
                  <h4 className="text-sm font-semibold text-silver mb-2">Tech Stack</h4>
                  <div className="flex flex-wrap gap-1">
                    {app.technologies.map((tech, idx) => (
                      <span
                        key={idx}
                        className="px-2 py-1 bg-gray-800 text-gray-300 text-xs rounded border border-gray-700"
                      >
                        {tech}
                      </span>
                    ))}
                  </div>
                </div>

                {/* Features */}
                <div>
                  <h4 className="text-sm font-semibold text-silver mb-2">Key Features</h4>
                  <div className="space-y-1">
                    {app.features.slice(0, 2).map((feature, idx) => (
                      <div key={idx} className="text-xs text-gray-400 flex items-center">
                        <div 
                          className="w-1 h-1 rounded-full mr-2 flex-shrink-0"
                          style={{ backgroundColor: app.color }}
                        ></div>
                        {feature}
                      </div>
                    ))}
                    {app.features.length > 2 && (
                      <div className="text-xs text-gray-500">
                        +{app.features.length - 2} more features
                      </div>
                    )}
                  </div>
                </div>
              </div>

              {/* Accent Border */}
              <div 
                className="absolute bottom-0 left-0 right-0 h-0.5 transition-all duration-300"
                style={{ 
                  backgroundColor: hoveredApp === index ? app.color : 'transparent'
                }}
              ></div>
            </div>
          ))}
        </div>

        {/* Portfolio CTA */}
        <div className="text-center">
          <div className="glass-morphism rounded-2xl p-8 md:p-12 max-w-4xl mx-auto">
            <h3 className="text-2xl md:text-3xl font-bold text-silver mb-4">
              Ready to Build Something Amazing?
            </h3>
            <p className="text-gray-400 mb-8 text-lg">
              Let's discuss your project requirements and create a custom solution 
              that drives your business forward.
            </p>
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <button className="bg-silver text-black px-8 py-3 rounded-full font-semibold hover:bg-white transition-all duration-300 hover:scale-105">
                Start Your Project
              </button>
              <button className="border border-silver text-silver px-8 py-3 rounded-full font-semibold hover:bg-silver hover:text-black transition-all duration-300 hover:scale-105">
                View Full Portfolio
              </button>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default ShowcaseSection;